.search-icon {
  font-size: 1.8rem;
  cursor: pointer;
  display: inline-flex !important;
  align-items: center !important;
  justify-content: center !important;
  height: 100%;
}

.hero-section {
  background-color: #efefef;
}
.hero-section__img {
  height: 60rem;
}
.hero-section__left {
  position: relative;
}
.hero-section__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}